:root {
  color: #242424;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  background-color: rgba(255, 255, 255, .87);
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    .container {
      width: 320px;
      padding: 0 20px;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    .container {
      width: 768px;
      padding: 0 32px;
    }
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 32px;
  }
}

body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

h1, p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

