.hystmodal__opened, .hystmodal__shadow {
  position: fixed;
  left: 0;
  right: 0;
  overflow: hidden;
}

.hystmodal__shadow {
  width: 100%;
  pointer-events: none;
  z-index: 98;
  opacity: 0;
  background-color: #000;
  border: none;
  transition: opacity .15s;
  display: block;
  top: 0;
  bottom: 0;
}

.hystmodal__shadow--show {
  pointer-events: auto;
  opacity: .6;
}

.hystmodal {
  -webkit-overflow-scrolling: touch;
  opacity: 1;
  pointer-events: none;
  z-index: 99;
  visibility: hidden;
  flex-flow: column;
  justify-content: flex-start;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.hystmodal--active {
  opacity: 1;
}

.hystmodal--active, .hystmodal--moved {
  pointer-events: auto;
  visibility: visible;
}

.hystmodal__wrap {
  width: 100%;
  min-height: 100%;
  flex-flow: column;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
}

.hystmodal__window {
  box-sizing: border-box;
  width: 600px;
  max-width: 100%;
  opacity: 0;
  background: #fff;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 50px 0;
  transition: transform .2s, opacity .2s;
  overflow: visible;
  transform: scale(.9);
}

.hystmodal--active .hystmodal__window {
  opacity: 1;
  transform: scale(1);
}

.hystmodal__close {
  z-index: 10;
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border: none;
  outline: none;
  font-size: 0;
  display: block;
  position: absolute;
  top: 0;
  right: -40px;
}

@media (max-width: 767px) {
  .hystmodal__close {
    width: 24px;
    height: 24px;
    top: 10px;
    right: 10px;
  }

  .hystmodal__window {
    margin: 0;
  }
}

.button {
  letter-spacing: -.3px;
  width: 139px;
  height: 48px;
  text-align: center;
  border-radius: 74px;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  font-family: Roboto Medium;
  font-size: 16px;
  line-height: 1.5;
  display: flex;
}

.buttonVideo {
  color: #111;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
}

.buttonInfo {
  color: #fff;
  background-color: #111;
  border: 1px solid #fff;
}

.hystmodal__window {
  width: 700px;
  height: 400px;
  background: #111;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

iframe {
  width: 100%;
  height: 100%;
}

.hystmodal__close {
  z-index: 100;
  width: 10.5px;
  height: 10.5px;
  stroke: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("icon-x.90b19e0b.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border: none;
  outline: none;
  font-size: 0;
  display: block;
  position: absolute;
  top: 40px;
  right: 40px;
}

.hystmodal__close:focus, .hystmodal__close:hover {
  outline-offset: 2px;
  stroke: #f87719;
  outline: none;
}

.catalog__gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .catalog__gallery {
    justify-content: flex-start;
    row-gap: 28px;
  }
}

@media screen and (min-width: 1280px) {
  .catalog__gallery {
    row-gap: 32px;
  }
}

.movie-card {
  cursor: pointer;
  width: 280px;
  height: 406px;
  border-radius: 5px;
  flex-basis: calc(100% - 32px);
  text-decoration: none;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.movie-card:hover, .movie-card:focus {
  transform: scale(1.08);
}

@media screen and (min-width: 768px) {
  .movie-card {
    width: 224px;
    height: 325px;
    flex-basis: calc(33.3333% - 10.6667px);
  }
}

@media screen and (min-width: 1280px) {
  .movie-card {
    width: 395px;
    height: 574px;
    flex-basis: calc(33.3333% - 10.6667px);
  }
}

.movie-card__img {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%), url("image-not-found.d18e87a3.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 5px;
}

.movie-card__info {
  height: 40px;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 12px;
  padding-left: 12px;
  padding-right: 12px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .movie-card__info {
    height: 46px;
  }
}

@media screen and (min-width: 1280px) {
  .movie-card__info {
    height: 54px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.movie-card__title {
  text-transform: uppercase;
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.67;
}

.movie-card__ganre-rating-wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.movie-card__genre-year {
  color: #b7b7b7;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.67;
}

@media screen and (min-width: 1280px) {
  .movie-card__title, .movie-card__genre-year {
    font-size: 20px;
    line-height: 1.15;
  }
}

.rating {
  align-items: flex-end;
  font-size: 12px;
  line-height: .75;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .rating {
    font-size: 20px;
    line-height: 1.15;
  }
}

.rating__body {
  border: 1px;
  position: relative;
}

.rating__body:before {
  content: "★★★★★";
  color: rgba(0, 0, 0, 0);
  -webkit-text-stroke: 1px #f84119;
  display: block;
}

.rating__active {
  width: 40px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating__active:before {
  content: "★★★★★";
  width: 100%;
  height: 100%;
  background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
}

.rating__items {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.rating__item {
  height: 100%;
  opacity: 0;
  flex: 0 0 20%;
}



